"use client";

import type { VisualInterfaceProps } from "@/features/workspace/types/ui";

export default function VisualInterface({
  onExport,
  canExport,
  stats,
  isCyclic,
  semanticIssues,
  containerRef,
}: VisualInterfaceProps) {
  const issues = semanticIssues ?? [];
  return (
    <section className="rounded-2xl border bg-white p-5 shadow-sm" style={{ borderColor: 'var(--border)', backgroundColor: 'var(--bg-primary)' }}>
      <div className="flex flex-wrap items-center justify-between gap-3">
        <div className="flex items-center gap-3">
          <h2 className="text-lg font-semibold" style={{ color: 'var(--text-primary)' }}>Graph Preview</h2>
          <span className="rounded-full border px-3 py-1 text-[10px] uppercase tracking-[0.3em]" style={{ borderColor: 'var(--border)', color: 'var(--text-muted)' }}>
            Cytoscape
          </span>
          {isCyclic ? (
            <span
              className="rounded-full border px-3 py-1 text-[10px] uppercase tracking-[0.3em]"
              style={{ borderColor: 'var(--border)', color: 'var(--accent)' }}
              title="Cycle detected in graph"
            >
              Cyclic
            </span>
          ) : null}
        </div>
        <button
          className="rounded-full px-4 py-2 text-xs font-semibold uppercase tracking-[0.3em] text-white"
          style={{
            backgroundColor: 'var(--accent)',
            color: 'var(--bg-primary)',
            opacity: canExport ? 1 : 0.5,
            cursor: canExport ? 'pointer' : 'not-allowed'
          }}
          onClick={onExport}
          disabled={!canExport}
          type="button"
        >
          Export PNG
        </button>
      </div>

      <div
        ref={containerRef}
        className="mt-4 h-130 rounded-xl border"
        style={{ borderColor: 'var(--border)', backgroundColor: 'var(--bg-primary)' }}
      />

      <div className="mt-4 flex flex-wrap gap-2 text-xs" style={{ color: 'var(--text-muted)' }}>
        <span className="rounded-full px-3 py-1" style={{ backgroundColor: 'var(--bg-tertiary)', color: 'var(--text-secondary)' }}>
          nodes: {stats.nodes}
        </span>
        <span className="rounded-full px-3 py-1" style={{ backgroundColor: 'var(--bg-tertiary)', color: 'var(--text-secondary)' }}>
          edges: {stats.edges}
        </span>
      </div>

      <div className="mt-4 rounded-xl border p-4" style={{ borderColor: 'var(--border)', backgroundColor: 'var(--bg-secondary)' }}>
        <div className="flex items-center justify-between gap-3">
          <h3 className="text-sm font-semibold" style={{ color: 'var(--text-primary)' }}>Semantic Issues</h3>
          <span className="rounded-full px-3 py-1 text-[10px] uppercase tracking-[0.3em]" style={{ backgroundColor: 'var(--bg-tertiary)', color: 'var(--text-secondary)' }}>
            {issues.length === 0 ? "None" : `${issues.length} issue${issues.length === 1 ? "" : "s"}`}
          </span>
        </div>
        {issues.length === 0 ? (
          <p className="mt-3 text-xs" style={{ color: 'var(--text-muted)' }}>
            No semantic issues detected.
          </p>
        ) : (
          <div className="mt-3 flex flex-col gap-2 text-xs" style={{ color: 'var(--text-secondary)' }}>
            {issues.map((issue, index) => {
              if (issue.type === "edge-missing-node") {
                return (
                  <div key={`issue-${index}`} className="rounded-lg border px-3 py-2" style={{ borderColor: 'var(--border)' }}>
                    Missing node `{issue.missing}` referenced by edge {issue.edge.from} → {issue.edge.to}.
                  </div>
                );
              }
              return (
                <div key={`issue-${index}`} className="rounded-lg border px-3 py-2" style={{ borderColor: 'var(--border)' }}>
                  Unreachable node `{issue.nodeId}`.
                </div>
              );
            })}
          </div>
        )}
      </div>
    </section>
  );
}
