"use client";

import { useEffect, useRef, useState } from "react";
import type { Core } from "cytoscape";
import { Engine } from "@andro.dev/jsonflow-engine";
import { useTheme } from "@/contexts/ThemeContext";
import {
  type LayoutDirection,
  renderCytoscape,
} from "@/libs/renders/cytoscape";
import EditorInterface from "@/features/workspace/components/EditorInterface";
import VisualInterface from "@/features/workspace/components/VisualInterface";
import type { GraphStats, SchemaStatus, SemanticIssue } from "@/features/workspace/types/ui";

const starterJson = `{
  "type": "flow",
  "layout": { "direction": "LR" },
  "nodes": [
    { "id": "editor", "label": "Editor Layer" },
    { "id": "logic", "label": "Logic Layer" },
    { "id": "parser", "label": "Parser Layer" },
    { "id": "visual", "label": "Visualization Layer" },
    { "id": "layout", "label": "Layout Layer" }
  ],
  "edges": [
    { "from": "editor", "to": "logic", "label": "validate", "kind": "next", "link_type": "solid" },
    { "from": "logic", "to": "parser", "label": "transform", "kind": "next", "link_type": "solid" },
    { "from": "parser", "to": "visual", "label": "render-ready", "kind": "next", "link_type": "arrow" },
    { "from": "visual", "to": "layout", "label": "layout", "kind": "next", "link_type": "dash" }
  ]
}`;

const engine = new Engine();

export default function Workspace() {
  const { theme } = useTheme();
  const [raw, setRaw] = useState(starterJson);
  const [debouncedRaw, setDebouncedRaw] = useState(starterJson);
  const [error, setError] = useState<string | null>(null);
  const [errorType, setErrorType] = useState<"json" | "schema" | null>(null);
  const [parsed, setParsed] = useState<
    | {
        ok: true;
        graph: { layout?: { direction?: LayoutDirection } };
        engineGraph: { nodes: { id: string }[]; edges: { from: string; to: string }[] };
        meta: { isCyclic: boolean };
        semantic: SemanticIssue[];
      }
    | { ok: false; error: unknown }
  >({ ok: false, error: null });
  const [cy, setCy] = useState<Core | null>(null);
  const containerRef = useRef<HTMLDivElement | null>(null);
  const downloadRef = useRef<HTMLAnchorElement | null>(null);

  useEffect(() => {
    const handle = setTimeout(() => setDebouncedRaw(raw), 250);
    return () => clearTimeout(handle);
  }, [raw]);

  useEffect(() => {
    try {
      const json = JSON.parse(debouncedRaw);
      const result = engine.parse(json);
      if (!result.ok) {
        const issues = (result.error as { issues?: { path: (string | number)[]; message: string }[] }).issues;
        if (issues && issues.length > 0) {
          const first = issues[0];
          const path = first.path.length > 0 ? first.path.join(".") : "root";
          setErrorType("schema");
          setError(`Schema error at ${path}: ${first.message}`);
        } else {
          setErrorType("schema");
          setError("Schema validation failed.");
        }
      } else {
        setErrorType(null);
        setError(null);
      }
      setParsed(result);
    } catch (parseError) {
      const message = parseError instanceof Error ? parseError.message : "Invalid JSON";
      setErrorType("json");
      setError(`JSON parse error: ${message}`);
      setParsed({ ok: false, error: parseError });
    }
  }, [debouncedRaw]);

  const status: SchemaStatus = parsed.ok ? "ok" : "error";
  const stats: GraphStats = parsed.ok
    ? {
        nodes: parsed.engineGraph.nodes.length,
        edges: parsed.engineGraph.edges.length,
      }
    : { nodes: 0, edges: 0 };

  const renderGraph = () => {
    if (!containerRef.current) {
      return;
    }

    if (!parsed.ok) {
      if (errorType === "json") {
        setError(error ?? "JSON parse error.");
      } else if (errorType === "schema") {
        setError(error ?? "Schema validation failed.");
      } else {
        setError("Invalid JSON or schema validation failed.");
      }
      return;
    }

    setError(null);

    if (parsed.semantic.length > 0) {
      const missing = parsed.semantic.find((issue) => issue.type === "edge-missing-node");
      if (missing && missing.type === "edge-missing-node") {
        setError(
          `Edge ${missing.edge.from} → ${missing.edge.to} references missing node "${missing.missing}".`,
        );
        return;
      }
    }

    if (cy) {
      cy.destroy();
    }

    const result = renderCytoscape({
      container: containerRef.current,
      graph: parsed.graph,
      engineGraph: parsed.engineGraph,
      existingCy: cy,
    });
    if (!result.ok) {
      setError(result.error);
      return;
    }

    setCy(result.cy);
  };

  const exportImage = () => {
    if (!cy) {
      setError("Render a graph before exporting.");
      return;
    }

    const bgColor = theme === 'dark' ? '#0f172a' : '#ffffff';
    const png = cy.png({ full: true, bg: bgColor });
    if (downloadRef.current) {
      downloadRef.current.href = png;
      downloadRef.current.download = "jsonflow-graph.png";
      downloadRef.current.click();
    }
  };

  useEffect(() => {
    const handler = () => renderGraph();
    window.addEventListener("jsonflow:render", handler);
    return () => window.removeEventListener("jsonflow:render", handler);
  }, [parsed.ok, raw, error, errorType]);

  return (
    <main className="grid w-full gap-6 px-6 py-8 md:grid-cols-2">
      <a ref={downloadRef} className="hidden" />
      <EditorInterface
        value={raw}
        status={status}
        errorMessage={error}
        semanticIssues={parsed.ok ? parsed.semantic : undefined}
        onChange={setRaw}
        onRender={renderGraph}
      />
      <VisualInterface
        onExport={exportImage}
        canExport={Boolean(cy)}
        stats={stats}
        isCyclic={parsed.ok ? parsed.meta.isCyclic : undefined}
        semanticIssues={parsed.ok ? parsed.semantic : undefined}
        containerRef={containerRef}
      />
    </main>
  );
}
